body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font: 14px "Century Gothic", Futura, sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.container {
  margin-left: 15%;
  margin-right: 15%;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.footer {
  margin-bottom: 15px;
  text-align: center;
  color: whitesmoke;
  font-size: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #333;
}

.TwoCanvas {
  display: inline-block;
  border-style: solid;
  border-width: 1px;
  border-radius: 1%;
  display: block;
  overflow-y: hidden;
  transition: transform 0.3s ease-out;
  box-shadow: 20px 20px 1px -9px rgba(0, 0, 0, 0.15);
}

.TwoCanvas:hover {
  box-shadow: 20px 20px 1px -9px rgba(0, 0, 0, 0.15);
  transform: translateY(-5px);
}

.divTwo {
  z-index: 0;
  display: inline-block;
  -webkit-filter: blur(5px);
  -moz-filter: blur(5px);
  -o-filter: blur(5px);
  -ms-filter: blur(5px);
  filter: blur(5px);
}

.divTwo:hover {
  -webkit-filter: blur(0px);
  -moz-filter: blur(0px);
  -o-filter: blur(0px);
  -ms-filter: blur(0px);
  filter: blur(0px);
}

.sim-div {
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
}

.sim-div h1 {
  position: absolute;
  color: white;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
  z-index: 2;
}

.sim-div button {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  background: url(icons/maximise.png);
  width: 30px;
  height: 30px;
  border: none;  
  transition: transform 0.2s ease-out;
}
.sim-div *:hover {
  transform: translateY(-2px);
}
.sim-div:hover h1 {
  display: none;
}